// 商场选择框
<template>
  <div>
    <span id='text'>时间选择:</span>
    <el-radio-group v-model="dateTime" @change='submitTime' :style='widthSet'>
      <el-radio-button label="近7日"></el-radio-button>
      <el-radio-button label="近30日"></el-radio-button>
      <el-radio-button label="近1年"></el-radio-button>
      <el-radio-button label="自定义时间"></el-radio-button> 
    </el-radio-group>

  </div>
  
</template>
<script>

export default { 
  props:['timeSet'],
  data(){
    return {
      businessValue:'',//选择的商场
      businessList:[],//商场列表
      dateTime:'近7日',
      textWidth:''
    }
  },
  components:{

  },
  mounted(){
        this.textWidth=document.getElementById('text').offsetWidth
  },
  methods:{
    submitTime(val){
      this.$emit('timeGet',val)
    }
    },
  computed:{
    widthSet(){
      return `width:calc(90% - ${this.textWidth}px);`
    }
  },
  watch:{
    timeSet(val){
      this.dataTime=val
    }
  }

  }


</script>
<style  scoped>

 .user-radio span{
   padding: 12px 10px;
 }
 .set{
   padding: 0px 0px !important;
 }
 
</style>