<template>
  <div>
    <div>
        <crumbs menu='商场流量' subMenu='流量概况' /> 
      <div class="container sql">
        <el-row >
          <el-col :span='5' >
            <market @marketGet='marketGet'/>
          </el-col>
          <el-col :span='5'>
            <index-select :indexSet='index' @indexGet='indexGet' />
          </el-col>
          <el-col :span='7'  :offset='1'>
            <time-select @timeGet='timeGet' style="width:100%;margin-left:20px"></time-select>
          </el-col>
          <el-col :span='5'>
            <div class="block">
                <el-date-picker
                  @change="changeTime"
                  style="width:100%;"
                  :disabled='datePickerAble'
                  v-model="timeRange"
                  type="daterange"
                  :shortcuts="shortcuts"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  align="right">
                </el-date-picker>
            </div>
          </el-col>
        </el-row>
      </div>
        <el-card header="同比增长图" style="font-weight: bold;margin-top:10px">
          <div ref="myEchart1" style="height: 350px;width:100%;" >
            </div>
        </el-card>
        <el-card header="环比增长图" style="font-weight: bold;margin-top:10px">
          <div ref="myEchart2" style="height: 350px;width:100%;" >
            </div>
        </el-card>
    </div>
  </div>
</template>

<script>
import Crumbs from '../pages/Crumbs'
import MarketSelect from '../pages/indexSelect/MarketSelect'
import IndexSelect from '../pages/indexSelect/IndexSelect'
import TimeSelect from '../pages/TimeSelect'
import * as echarts from 'echarts'
import axios from 'axios'

export default {
    data() {
        return {
            marketName:'',
            indexName:'客流量',
            datePickerAble:true,
            time:'近7日',
            index:[{
                      value: '客流量',
                      label: '客流量',
                    }, {
                      value: '集客力',
                      label: '集客力',
                    }, {
                      value: '提袋率',
                      label: '提袋率',
                    }, {
                      value: '游逛深度',
                      label: '游逛深度',
                    }],
            shortcuts: [{
                text: '最近一周',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  return [this.$moment().subtract(7, 'days'), end]
                })()
              }, {
                text: '最近30天',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setMonth(start.get)
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                  return [this.$moment().subtract(1, 'months'), end]
                })()
              }, {
                text: '最近一年',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setFullYear(start.getFullYear()-1)
                  // start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  return [this.$moment().subtract(1, 'years'), end]
                })()
              }],
            timeRange: '',
            screenWidth: document.body.clientWidth,   
            chart1:{},
            chartData1:[],
            chart2:{},
            chartData2:[]

        };

    },
    methods: {
      marketGet(val){
      this.marketName=val
      },
      indexGet(val){
        this.indexName=val
      },
      timeGet(val){
        console.log(val)
        console.log(this.timeRange)
        this.time=val
        if(val=='自定义时间'){
          this.datePickerAble=false
        }else this.datePickerAble=true
      },
      timeIntval(startTime,endTime){
          let arr = []
          let moment=this.$moment
          startTime = moment(startTime).format('YYYY-MM-DD')
          endTime = moment(endTime).format('YYYY-MM-DD')
          while (moment(startTime).isBefore(endTime)) {
            arr.push(moment(startTime).format('MM-DD'))
            startTime = moment(startTime).add(1, 'days').format('YYYY-MM-DD')
          }
          arr.push(endTime)
          return arr
      },
      initChart1(chart,data){
            let myChart =echarts.init(chart);
            let option = {
                  tooltip: {
                      trigger: 'axis',
                      axisPointer: {
                          type: 'cross',
                          crossStyle: {
                              color: '#999'
                          }
                      }
                  },
                  dataZoom: [{
                      type: 'inside'
                  }, {
                      type: 'slider'
                  }],
                  toolbox: {
                      feature: {
                        dataZoom: {
                              yAxisIndex: false
                          },
                          dataView: {show: true, readOnly: false},
                          magicType: {show: true, type: ['line', 'bar']},
                          saveAsImage: {show: true},
                          
                          
                      }
                  },
                  legend: {
                      data: [`${this.$moment(this.timeRange[0]).format('YYYY')}年`,`${this.$moment(this.timeRange[0]).subtract(1, 'years').format('YYYY')}年`,'同比增长']
                    
                  },
                  xAxis: [
                      {
                          type: 'category',
                          data: this.timeIntval(this.timeRange[0],this.timeRange[1]),
                          axisPointer: {
                              type: 'shadow'
                          }
                      }
                  ],
                  yAxis: [
                      {
                          type: 'value',
                          name:this.indexName,
                          
                          axisLabel: {
                              formatter: '{value}'
                          }
                      },
                      {
                          type: 'value',
                          name: '同比增长',
                          
                          axisLabel: {
                              formatter: '{value} %'
                          }
                      }
                  ],
                  series: data
              };
            console.log(option.legend.data)
            console.log(option.series)
            myChart.setOption(option);
            return myChart
      },
      initChart2(chart,data){
            let myChart =echarts.init(chart);
            let option = {
                  tooltip: {
                      trigger: 'axis',
                      axisPointer: {
                          type: 'cross',
                          crossStyle: {
                              color: '#999'
                          }
                      }
                  },
                  dataZoom: [{
                      type: 'inside'
                  }, {
                      type: 'slider'
                  }],
                  toolbox: {
                      feature: {
                        dataZoom: {
                              yAxisIndex: false
                          },
                          dataView: {show: true, readOnly: false},
                          magicType: {show: true, type: ['line', 'bar']},
                          restore: {show: true},
                          saveAsImage: {show: true}
                      }
                  },
                  legend: {
                      data: ['当前期','上期','环比增长']
                    
                  },
                  xAxis: [
                      {
                          type: 'category',
                          data: this.timeIntval(this.timeRange[0],this.timeRange[1]),
                          axisPointer: {
                              type: 'shadow'
                          }
                      }
                  ],
                  yAxis: [
                      {
                          type: 'value',
                          name:this.indexName,
                          
                          axisLabel: {
                              formatter: '{value}'
                          }
                      },
                      {
                          type: 'value',
                          name: '环比增长',
                          
                          axisLabel: {
                              formatter: '{value} %'
                          }
                      }
                  ],
                  series: data
              };
            console.log(option.legend.data)
            console.log(option.series)
            myChart.setOption(option);
            return myChart
      },
      changeTime(){
        this.chart1.clear()
        this.chartData1=[]
        console.log(this.timeIntval(this.timeRange[0],this.timeRange[1]).length)
        if(this.indexName=='提袋率'){
            axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getTrafficProfile?lg=${this.timeIntval(this.timeRange[0],this.timeRange[1]).length}`).then(res=>{
              this.chartData1.push({
                // name:`${this.$moment(this.timeRange[0]).format('YYYY-DD')}`,
                name:`${this.$moment(this.timeRange[0]).format('YYYY')}年`,
                type:'bar',
                data:res.data.rate.data
              })
                this.chart1=this.initChart1(this.$refs.myEchart1,this.chartData1)

            })
            axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getTrafficProfile?lg=${this.timeIntval(this.timeRange[0],this.timeRange[1]).length}`).then(res=>{
              this.chartData1[1]={
                name:`${this.$moment(this.timeRange[0]).subtract(1, 'years').format('YYYY')}年`,
                type:'bar',
                data:res.data.rate.data
              }
                this.chart1=this.initChart1(this.$refs.myEchart1,this.chartData1)
              this.chartData1[2]={
                name:'同比增长',
                type:'line',
                yAxisIndex:1,
                data:this.rate,
                symbol: "none"
              }
                this.chart1=this.initChart1(this.$refs.myEchart1,this.chartData1)

            })
        }
        else{
          axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getTrafficProfile?lg=${this.timeIntval(this.timeRange[0],this.timeRange[1]).length}`).then(res=>{
          this.chartData1.push({
            // name:`${this.$moment(this.timeRange[0]).format('YYYY-DD')}`,
            name:`${this.$moment(this.timeRange[0]).format('YYYY')}年`,
            type:'bar',
            data:res.data.list.data
          })
            this.chart1=this.initChart1(this.$refs.myEchart1,this.chartData1)

          })
          axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getTrafficProfile?lg=${this.timeIntval(this.timeRange[0],this.timeRange[1]).length}`).then(res=>{
            this.chartData1[1]={
              name:`${this.$moment(this.timeRange[0]).subtract(1, 'years').format('YYYY')}年`,
              type:'bar',
              data:res.data.list.data
            }
              this.chart1=this.initChart1(this.$refs.myEchart1,this.chartData1)
            this.chartData1[2]={
              name:'同比增长',
              type:'line',
              yAxisIndex:1,
              data:this.rate,
              symbol: "none"
            }
              this.chart1=this.initChart1(this.$refs.myEchart1,this.chartData1)
          })
        }
        this.changeTime2()
      },
      changeTime2(){
        this.chart2.clear()
        this.chartData2=[]
        console.log(this.timeIntval(this.timeRange[0],this.timeRange[1]).length)
        
        axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getTrafficProfile?lg=${this.timeIntval(this.timeRange[0],this.timeRange[1]).length+1}`).then(res=>{
          if(this.indexName=='提袋率'){
            this.chartData2.push({
            name:`上期`,
            type:'bar',
            data:res.data.rate.data
            })
            this.chartData2.push({
              // name:`${this.$moment(this.timeRange[0]).format('YYYY-DD')}`,
              name:`当前期`,
              type:'bar',
              data:res.data.rate.data.slice(1)
            })
            this.chartData2.push({
              name:'环比增长',
              type:'line',
              yAxisIndex:1,
              data:this.rate2,
              symbol: "none"
            })
          }else{
            this.chartData2.push({
            name:`上期`,
            type:'bar',
            data:res.data.list.data
            })
            this.chartData2.push({
              // name:`${this.$moment(this.timeRange[0]).format('YYYY-DD')}`,
              name:`当前期`,
              type:'bar',
              data:res.data.list.data.slice(1)
            })
            this.chartData2.push({
              name:'环比增长',
              type:'line',
              yAxisIndex:1,
              data:this.rate2,
              symbol: "none"
            })
          }
          
            this.chart2=this.initChart2(this.$refs.myEchart2,this.chartData2)
        })
      }
    },
    mounted(){
      this.timeRange=[this.$moment().subtract(7, 'days').format(),this.$moment()]
      const that = this
        window.onresize = () => {
        return (() => {
            window.screenWidth = document.body.clientWidth
            that.screenWidth = window.screenWidth
        })()
    }
    //同比
    if(this.indexName=='提袋率'){
        axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getTrafficProfile?lg=${this.timeIntval(this.timeRange[0],this.timeRange[1]).length}`).then(res=>{

          this.chartData1.push({
            // name:`${this.$moment(this.timeRange[0]).format('YYYY-DD')}`,
            name:`${this.$moment(this.timeRange[0]).format('YYYY')}年`,
            type:'bar',
            data:res.data.rate.data
          })
            this.chart1=this.initChart1(this.$refs.myEchart1,this.chartData1)

        })
        axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getTrafficProfile?lg=${this.timeIntval(this.timeRange[0],this.timeRange[1]).length}`).then(res=>{
          this.chartData1[1]={
            name:`${this.$moment(this.timeRange[0]).subtract(1, 'years').format('YYYY')}年`,
            type:'bar',
            data:res.data.rate.data
          }
            this.chart1=this.initChart1(this.$refs.myEchart1,this.chartData1)
          this.chartData1[2]={
            name:'同比增长',
            type:'line',
            yAxisIndex:1,
            data:this.rate,
            symbol: "none"
          }
            this.chart1=this.initChart1(this.$refs.myEchart1,this.chartData1)

        })
    //环比
    }else{
        axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getTrafficProfile?lg=${this.timeIntval(this.timeRange[0],this.timeRange[1]).length}`).then(res=>{

          this.chartData1.push({
            // name:`${this.$moment(this.timeRange[0]).format('YYYY-DD')}`,
            name:`${this.$moment(this.timeRange[0]).format('YYYY')}年`,
            type:'bar',
            data:res.data.list.data
          })
            this.chart1=this.initChart1(this.$refs.myEchart1,this.chartData1)

        })
        axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getTrafficProfile?lg=${this.timeIntval(this.timeRange[0],this.timeRange[1]).length}`).then(res=>{
          this.chartData1[1]={
            name:`${this.$moment(this.timeRange[0]).subtract(1, 'years').format('YYYY')}年`,
            type:'bar',
            data:res.data.list.data
          }
            this.chart1=this.initChart1(this.$refs.myEchart1,this.chartData1)
          this.chartData1[2]={
            name:'同比增长',
            type:'line',
            yAxisIndex:1,
            data:this.rate,
            symbol: "none"
          }
            this.chart1=this.initChart1(this.$refs.myEchart1,this.chartData1)

        })

    }
   //环比
    axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getTrafficProfile?lg=${this.timeIntval(this.timeRange[0],this.timeRange[1]).length+1}`).then(res=>{
      if(this.indexName=='提袋率'){
        this.chartData2.push({
            name:`上期`,
            type:'bar',
            data:res.data.rate.data
            })
            this.chartData2.push({
              // name:`${this.$moment(this.timeRange[0]).format('YYYY-DD')}`,
              name:`当前期`,
              type:'bar',
              data:res.data.rate.data.slice(1)
            })
            this.chartData2.push({
              name:'环比增长',
              type:'line',
              yAxisIndex:1,
              data:this.rate2,
              symbol: "none"
            })
      } else{
            this.chartData2.push({
            // name:`${this.$moment(this.timeRange[0]).format('YYYY-DD')}`,
            name:`上期`,
            type:'bar',
            data:res.data.list.data
          })
          this.chartData2.push({
            // name:`${this.$moment(this.timeRange[0]).format('YYYY-DD')}`,
            name:`当前期`,
            type:'bar',
            data:res.data.list.data.slice(1)
          })
          this.chartData2.push({
            name:'环比增长',
            type:'line',
            yAxisIndex:1,
            data:this.rate2,
            symbol: "none"
          })
      }
      
      this.chart2=this.initChart2(this.$refs.myEchart2,this.chartData2)

    })
    
    //
    },
    components:{
    crumbs:Crumbs,
    market:MarketSelect,
    indexSelect:IndexSelect,
    timeSelect:TimeSelect,
  },
  watch:{
    time(val){
      if(val==='近7日'){
        this.timeRange=[this.$moment().subtract(7, 'days'),this.$moment()]
      }
      if(val==="近30日"){
        this.timeRange=[this.$moment().subtract(1, 'months'),this.$moment()]
      }
      if(val==="近1年"){
        this.timeRange=[this.$moment().subtract(1, 'year'),this.$moment()]
      }
      this.changeTime()
      this.changeTime2()
    },
    indexName(){
      this.changeTime()
      this.changeTime2()
    },
    marketName(){
      this.changeTime()
      this.changeTime2()
    },
    screenWidth(val){
        // 为了避免频繁触发resize函数导致页面卡顿，使用定时器
        if(!this.timer){
            // 一旦监听到的screenWidth值改变，就将其重新赋给data里的screenWidth
            this.screenWidth = val
            this.timer = true
            let that = this
            setTimeout(function(){
                // 打印screenWidth变化的值
                console.log(that.screenWidth)
                that.timer = false
                that.chart1.resize()
                that.chart2.resize()
            },400)
        }
    },
  },
  computed:{
    rate(){
      let arr=[]
      console.log(this.chartData1[0].data)
      for(let i=0;i<this.chartData1[0].data.length;i++){
        arr.push(
          (((this.chartData1[0].data[i]-this.chartData1[1].data[i])/this.chartData1[1].data[i]).toFixed(4)*100)
        )
      }
      console.log(arr)
      return arr
    },
    rate2(){
      let arr=[]
      console.log(this.chartData2[0].data)
      for(let i=0;i<this.chartData2[0].data.length;i++){
        arr.push(
          (((this.chartData2[0].data[i]-this.chartData2[1].data[i])/this.chartData2[1].data[i]).toFixed(4)*100)
        )
      }
      console.log(arr)
      return arr
    }
  }
}

</script>
<style lang="css" scoped>

</style>